const tooltip = () => {
	$(".tooltip-btn").on("click", function (e) {
		e.preventDefault();
		let tooltipTop = $(this).offset().top;
		let tooltipLeft = $(this).offset().left;
		let tooltipHeight = $(this).outerHeight();
		let st = $(window).width();

		$(".tooltip-text__body").html($(".tooltip__inner").html());

		if (st > 767) {
			$(".tooltip-text").css({ top: tooltipTop + tooltipHeight - 120, left: tooltipLeft + 50 });
		} else {
			$(".tooltip-text").css({ top: "50%", left: "50%" });
		}

		$(".tooltip-text").toggleClass("tooltip-text--show");
	});

	jQuery(function ($) {
		$(".btn--cross").on("click", function () {
			console.log("cross");
			$(".tooltip-text").toggleClass("tooltip-text--show");
		});
	});

	$(window).resize(function () {
		let st = $(window).width();

		toltipDesktopResize();
	});

	function toltipDesktopResize() {
		let st = $(window).width();

		if (st < 768) {
			if ($(".tooltip-text").length) {
				$(".tooltip-text").css({ top: 0, left: 0 });
			}
		}
	}

	jQuery(function ($) {
		$(document).mouseup(function (e) {
			let div = $(".tooltip-text");
			let user_bag = $(".tooltip-btn");
			if (!div.is(e.target) && div.has(e.target).length === 0) {
				if (!user_bag.is(e.target) && user_bag.has(e.target).length === 0) {
					div.removeClass("tooltip-text--show");
				}
			}
		});
	});
};

export default tooltip;
