const slider_mobile = () => {
	let slider_mobile_card = $(".slider-mobile--card").slick({
		dots: true,
		dotsClass: "slider-mobile__dots slider-mobile__dots--card",
		infinite: false,
		speed: 300,
		slidesToShow: 1,
		slidesToScroll: 1,
		prevArrow: '<div class="slider-mobile__arrow-left slider-mobile__arrow-left--card"></div>',
		nextArrow: '<div class="slider-mobile__arrow-right slider-mobile__arrow-right--card"></div>',
	});
};

export default slider_mobile;
